.App {
  width: 100%;
  text-align: center;
  background-color: #c3fff5;
  height: 100vh;
  margin: 30px 0;
  border: 2px solid #16a48a;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.result {
  padding: 0.5em;
  font-size: 2rem;
  height: 3em;
}

.btn-prime {
  font-size: 18px;
  color: #16a48a;
  padding: 4px;
}
